<template>
<div class="flex">
  <div class="UserInfo">
    <h3>Данные игрока</h3>
    Id: {{User.id}}<br/>
    Логин: {{User.login}}<br/>
    Почта: {{User.email}}<br/>
  </div>
  <div class="GamesInfo">
    <h1>Сохраненные игры</h1>
    <table border="1">
      <thead>
        <td>Номер</td>
        <td>Название</td>
        <td>Описание</td>
        <td></td>
        <td></td>
      </thead>
      <tr>
        <td><input type="text"></td>
        <td><input type="text"></td>
        <td><input type="text"></td>
        <td><input type="button" value="Загрузить"></td>
        <td><input type="button" value="Сохранить"></td>
      </tr>
    </table>
  </div>
</div>
</template>

<script>
import { mapActions, mapGetters} from 'vuex'
import axios from "axios";

export default {
  data(){
      return{
        User:{},
      }
  },
  methods:{
    ...mapActions('auth',['login']),
    ...mapActions('auth',['logout']),
    ...mapGetters('auth',['isAuthenticated']),
  },
  async beforeMount()
  {
    let response = await axios("http://localhost:2500/user/", {
      method: "post",
    });

    this.User = response.data.user;

    console.log(response)
  }
}
</script>
<style scoped>
.flex{
  display:flex;
  width:90vh;
}
.UserInfo{
  width:40%
}
.GamesInfo{
  width:60%
}
</style>